// 引用模板
//vue
import Vue from 'vue';
//路由插件
import Router from 'vue-router';
import indexPage from './components/header.vue'
import homePage from './views/home.vue'
import aboutPage from './views/about.vue'
import letConst from './es6_study/letConst.vue'
import classExtendsSuper from './es6_study/classExtendsSuper.vue'

Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            component:homePage
        },
        {
            path:'/about',
            component:aboutPage
        },
        {
            path:'/letConst',
            component:letConst
        },
        {
            path:'/classExtendsSuper',
            component:classExtendsSuper
        }
    ]
})